ata 內底，臺語文需要安怎收錄，無仝款的輸入法相衝突的時陣又閣欲安怎處理？閣來佇咧小眾語言閣會當如何用 Wikidata 來進行語言的保存佮紀錄，這改就講予你知。")])]),_c('section',{staticClass:"speaker"},[_c('h2',[_vm._v("王文岳")]),_c('img',{attrs:{"src":"img/speaker_alllen.jpg"}}),_c('p',[_vm._v("李梅樹紀念館資訊組召集人，Wikidata Taiwan 社群共同主持人")])])]),_c('section',{staticClass:"session"},[_c('p',{staticClass:"time"},[_vm._v("10:20-11:00")]),_c('section',{staticClass:"talk"},[_c('h1',[_vm._v("完全用 Kamigo 做出來ê Muscle-man LINE Bot")]),_c('p',[_vm._v("寫 line bot ê工具，逐擺攏是 copy 進前ê project 來走。「卡米狗」 ê老爸做出來這欲hō͘逐家用ê framework - Kamigo，這擺ê LINE bot 著是全全用這來做--ê，tī遮會kā逐家講會安怎用，而且是安怎幫助我用 Ruby 來寫出我家己紀錄練身體ê line bot。")])]),_c('section',{staticClass:"speaker"},[_c('h2',[_vm._v("林家煜")]),_c('img',{attrs:{"src":"img/speaker_nijia.jpeg"}}),_c('p',[_vm._v("NiJia 是一ê tī台中浪流連 26 年ê少年仔，平常時無聊著愛寫「LINE bot」來耍，著因為按呢煞無小心變成 Chatbot Developers Taiwan 管理員之一，最近tú變成 LINE API Expert，歡迎逐家來討論抑是beh開講我攏真歡迎！")])])]),_c('section',{staticClass:"session"},[_c('p',{staticClass:"time"},[_vm._v("11:10-11:50")]),_c('section',{staticClass:"talk"},[_c('h1',[_vm._v("Chit ki chhiú ū ba̍kchiu (Hands with eyes)")]),_c('p',[_vm._v("TGH (Taiwan Gods Hand project, 台灣神手 開源義肢) 開發 chiâⁿ 多開源 ê 人工肢体、目的是爲着卜乎有需要 ê 人會凍做伙舞、鬥陣學。TGH 人工肢体 ê 技術不時 teh 進步。眼前上新 ê 人工手號做 TGHiii AICGS。合着 AI 物体探測 ê 功能、乎未來 ê 人工手 ē 曉認 bat 物件、通成做閣卡利便 ê kesi。")])]),_c('section',{staticClass:"speaker"},[_c('h2',[_vm._v("Tēⁿ Hiàn Kun")]),_c('img',{attrs:{"src":"img/speakler_thk.jpg"}}),_c('p',[_vm._v("本身是工程師、專長是電腦圖影處理 (computer vision, image processing) kap 一卦機器學習 (machine learning)。有一 ê 蓋久無 teh 顧 ê YouTube 網路台: http://bit.ly/2vFI7je 。上 chia̍p 用 ê OS 是 Lubuntu/Ubuntu (自 6.04 用到今)、上 chia̍p 寫 ê 電腦程序語言是 Phaisiâ (Python) kap Bash。大部分 khangkhòe 朗是倚靠 Open Source ê 軟体 teh 處理。那無 Vim góa ē 死.")])])]),_c('section',{staticClass:"session"},[_c('p',{staticClass:"time"},[_vm._v("13:00-13:40")]),_c('section',{staticClass:"talk"},[_c('h1',[_vm._v("Chhōe台語來Phah")]),_c('p',[_vm._v("紹介 ChhoeTaigi 台語辭典網站、app，和手機仔 PhahTaigi 台語輸入法ê開放專案現況kap未來規劃。")])]),_c('section',{staticCl