，比起傳統錄音方式koh較khin-khó，予語音應用tsiânn多元。")]),_c('p',[_vm._v("意傳參考LJ Speech ê概念，準備台灣話語料庫，kah模型訓練ê script公開，予APP開發、合成技術研究、文化保存團體，ē-sái分工注心做ka-tī專長ê代誌。")]),_c('p',[_vm._v("Demo Bāng-tsí: https://suisiann.ithuan.tw/")])]),_c('section',{staticClass:"speaker"},[_c('h2',[_vm._v("Ì-thuân Kho-ki - Sih Sîng-hông")]),_c('img',{attrs:{"src":"img/speaker_singhong.png"}}),_c('p',[_vm._v("意傳科技是台語文專業ê資訊公司，阮ê目標是用科技推sak台語，向望透過阮ê技術kah台語文專長，hōo台語ê影音製作kah生活應用koh khah利便。")]),_c('p',[_vm._v("現此時ê服務有台語字幕製作、台語口白文稿翻譯，嘛有台語輸入法、語音合成、語料庫等等資訊技術。")])])]),_c('section',{staticClass:"session"},[_c('p',{staticClass:"time"},[_vm._v("16:10-16:40")]),_c('section',{staticClass:"talk"},[_c('h1',[_vm._v("Lightning Talk")]),_c('p',[_vm._v("COSCUP 傳統好戲，五分鐘展工夫！")])]),_c('section',{staticClass:"speaker"},[_c('h2',[_vm._v("可能著是你！")]),_c('img',{attrs:{"src":"img/coscup.png"}}),_c('p',[_vm._v("kah開源有關係ê物件，攏歡迎逐家現場找服務台報名。（名額有限，請盡量用台語講演。）")])])])]),_c('p',[_vm._v("不管你是台語輾轉到會當主持婚宴ê老司機，抑是勉強聽有m̄-koh一句講都bē曉話ê菜鳥仔，攏歡迎來參加這場有夠純koh豐沛ê台語盛會，看咱ê Taiwanese 和 Open Source kap-kap做夥會當變出啥物báng！")]),_c('section',{staticClass:"hoagi"},[_c('p',[_vm._v("不管你是台語溜到能唱 Rap 的老司機，還是勉強聽懂但講不太出來的菜鳥，都歡迎來參加這場台語盛會，看 Taiwanese 跟 Open Source 跟的混搭能激盪出什麼火花！")])])]),_c('div',{staticClass:"spotlight"},[_c('h1',{staticClass:"font-black"},[_vm._v("活動資訊")]),_c('ul',{staticClass:"bullets"},[_c('li',[_vm._v("時間：2019/12/21（六） 09:30 - 17:00")]),_c('li',[_vm._v("地點：國立臺中科技大學 中技大樓 2F 多媒體會議室 （404 台中市北區錦平街 40 號，"),_c('a',{attrs:{"href":"https://www.nutc.edu.tw/files/11-1000-150.php"}},[_vm._v("交通資訊")]),_vm._v("、"),_c('a',{attrs:{"href":"https://tools.wmflabs.org/geohack/geohack.php?params=24_9_8_N_120_41_1_E"}},[_vm._v("地點座標 / GeoLocation")]),_vm._v("）")]),_c('li',[_vm._v("報名時間：2019/12/04 12:00 (+0800)")])])]),_c('div',{staticClass:"spotlight"},[_c('h1',{staticClass:"font-black"},[_vm._v("贊助資訊")]),_c('p',[_vm._v("COSCUP 是由全志工籌辦舉辦的活動，活動也是開放大眾免費報名，我們誠摯地邀請台中在地或支持開源的企業組織成為本專場贊助商。")]),_c('p',[_vm._v("歡迎來信洽詢 "),_c('a',{staticClass:"font-black",attrs:{"href":"mailto:sponsorship@